import axios from 'axios';

import {
  message,
} from 'ant-design-vue';

const headers = {
  'Content-Type': 'application/json',
};
const delayHideSeconds = 2;
const api = axios.create({
  baseURL: 'https://taskcreator.rocket-lab.ru/taskCreator/api',
  headers,
});
api.interceptors.response.use((response) => {
  switch (response.config.method) {
    case 'post':
      message.success(' ', delayHideSeconds);
      break;
    case 'put':
      message.success(' ', delayHideSeconds);
      break;
    case 'delete':
      message.success(' ', delayHideSeconds);
      break;
    default:
      break;
  }
  return response;
}, (error) => {
  switch (error.config.method) {
    case 'post':
      message.error('  ', delayHideSeconds);
      break;
    case 'put':
      message.error('  ', delayHideSeconds);
      break;
    case 'delete':
      message.error('  ', delayHideSeconds);
      break;
    case 'get':
      message.error('  ', delayHideSeconds);
      break;
    default:
      break;
  }
  return Promise.reject(error);
});

export default api;
